import React, { useState, useCallback } from 'react';
// import Teacher from './teacher/Teacher'
import { DualPanel } from './DualPanel';
import { ChatWindow } from './teacher/ChatWindow';
import MetaView from './metaview/MetaView';
import { Button } from '@mui/material';
import { useConversation } from './conversation/context';
import { GPTMenuBar } from './GPTMenuBar';
import "./Discussion.css";

function Discussion() {
  const [metaOpen, setMetaOpen] = useState(false);

  const handleMetaOpen = () => setMetaOpen(true);
  const handleMetaClose = () => setMetaOpen(false);

  return (
    <>
      <MenuBar handleMetaOpen={handleMetaOpen} handleMetaClose={handleMetaClose} metaOpen={metaOpen} />
      <DualPanel
        main={(props) => (<ChatWindow {...props} />)}
        left={(props) => (<MetaView {...props} />)}
        detailViewOpen={metaOpen}
        closeButton={true}
        onClose={handleMetaClose}
      />
    </>
  );
}

function MenuBar(props: { handleMetaOpen: () => void, handleMetaClose: () => void, metaOpen: boolean }) {
  // const { saveToFile, loadFromFile, resetConversation } = useConversation();
  const { state, dispatch, actions } = useConversation();


  const [settingsOpen, setSettingsOpen] = useState(false);
  return (
    <GPTMenuBar label="Assistant" metaOpen={props.metaOpen}>
      <Button variant="contained" onClick={props.metaOpen ? props.handleMetaClose : props.handleMetaOpen} sx={{
        marginRight: { xs: '2px', sm: '4px', md: '6px' },
      }}>
        Chats
      </Button>
    </GPTMenuBar >
  );
}

export default Discussion;