import React, { useState, useCallback } from 'react';
import { Box, Typography } from '@mui/material';
import Discussion from './Discussion';
import Writer from './writer/Writer';
import Drawer from '@mui/material/Drawer';
import Button from '@mui/material/Button';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import MenuIcon from '@mui/icons-material/Menu';

export enum Mode {
  Discussion = "Discussion",
  Assistant = "Assistant",
  Writer = "Writer",
}

function Assistant() {
  const [mode, setMode] = useState(Mode.Discussion);

  let obj: JSX.Element;
  switch (mode) {
    case Mode.Discussion:
      obj = <Discussion />;
      break;
    case Mode.Writer:
      obj = <Writer />;
      break;
    default:
      throw new Error(`Unknown mode: ${mode}`);
  }

  // Wrap in drawer
  return (
    // <GlobalMenu setMode={setMode}>
    <div className="root" style={{ height: "100%", width: "100%" }}>
      {obj}
    </div>
    // </GlobalMenu>
  )
}

function GlobalMenu(props: { children: JSX.Element, setMode: (mode: Mode) => void }) {
  // A small button always floats on the top-left, opening a drawer with some global options
  const [drawerOpen, setDrawerOpen] = useState(false);

  const handleDrawerOpen = () => setDrawerOpen(true);

  return (
    <>
      <Button style={{
        position: "absolute",
        top: "12px",
        left: "0px",
        padding: "0px",
        margin: "0px",
        zIndex: 1000,
        // White
        color: "white"
      }} onClick={handleDrawerOpen}>
        <MenuIcon />
      </Button>
      <Drawer
        anchor="left"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
      >
        <List>
          <ListItemButton onClick={() => {
            props.setMode(Mode.Discussion);
            setDrawerOpen(false);
          }}>
            <ListItemText primary="Discussion" />
          </ListItemButton>
          <ListItemButton onClick={() => {
            props.setMode(Mode.Writer);
            setDrawerOpen(false);
          }}>
            <ListItemText primary="Writer" />
          </ListItemButton>
        </List>
      </Drawer>
      {props.children}
    </>
  );
}

export default Assistant;